@mixin mq($range) {
  $mobile: 480px;
  $tablet: 768px;
  $desktop: 1366px;

  @if $range ==mobile {
    @media (min-width: $mobile) {
      @content;
    }
  }

  @if $range ==tablet {
    @media (min-width: $tablet) {
      @content;
    }
  }

  @if $range ==desktop {
    @media (min-width: $desktop) {
      @content;
    }
  }
}

